// === Ââç‰∏â O(n^2); ‰∏≠Âõõ O(n * log n); Âêé‰∫å O(n+ k); Âü∫Êéí O(O * k) === //

// === Á®≥ÂÆöÊÄßÔºö === //
// === ÈÄâÊã© / Â∏åÂ∞î / Âø´Êéí / Â†ÜÊéí ‰∏çÁ®≥ÂÆö === //

import deepCopy from '../../../lib/deepCopy'

/**
 * ‰∫§Êç¢‰ΩçÁΩÆ
 * // === ÊäÄÂ∑ßÔºöÂà©Áî®‰ª£Á†ÅÊâßË°åÈ°∫Â∫èÔºåÂêé‰∏Ä‰∏™ËµãÂÄº‰∏∫Ââç‰∏Ä‰∏™ÔºåËÄåÂâç‰∏Ä‰∏™ËµãÂÄº‰∏∫Ââç‰∏Ä‰∏™ÁöÑË°å‰∏∫Âú®Êï∞ÁªÑÁöÑÁ¨¨‰∫åÈ°πËøõË°å === //
 * @param {Array} arr - Êï∞ÁªÑ
 * @param {Number} prev - Ââç‰∏Ä‰∏™Á¥¢Âºï
 * @param {Number} next - Âêé‰∏Ä‰∏™Á¥¢Âºï
 * @private
 */
const swap = (arr, prev, next) => {
  arr[next] = [arr[prev], arr[prev] = arr[next]][0]
}

/**
 * Êü•ÊâæÂØπË±°Â±ûÊÄß
 * @param {Object} obj ÂØπË±°
 * @param {Array} property - Â±ûÊÄß‰∏∫È°∫Â∫èÁöÑÊï∞ÁªÑÔºåÂ¶ÇÔºö{one: {two: '1'}} ‰∏∫ ['one', 'two']
 * @private
 * @return {String|Number}
 */
const findProperty = (obj, property) => {
  let i = 0
  const len = property.length

  if (typeof obj !== 'object') {
    return obj
  }

  while (i < len) {
    let item = property[i]
    if (obj[item] !== 'undefined') {  // === Ê≠§Â§ÑÔºåÂà§Êñ≠‰∏Ä‰∏™Â±ûÊÄßÊòØÂê¶Â≠òÂú®Ôºå‰∏çËÉΩÁî®ÈöêÂºèËΩ¨Êç¢ÔºåÂõ†‰∏∫ÊúâÂèØËÉΩÂ±ûÊÄßÂÄº‰∏∫0, false, undefined, '', null‰ªéËÄåÂà§Êñ≠ÈîôËØØ === //
      obj = obj[item]
    } else {
      return obj
    }

    ++i
  }

  return obj
}

/**
 * ÊèíÂÖ•ÊéíÂ∫è
 * // === ÊèíÂÖ•ÊéíÂ∫è === //
 * // === ÊÄùÊÉ≥Ôºö[a, b, c, d, e], Â§ñÂæ™ÁéØ‰ªébÂºÄÂßãÔºåÂÜÖÂæ™ÁéØÊØîËæÉb‰πãÂâçÁöÑÂÖÉÁ¥†ÔºåËã•aÊØîbÂ§ßÔºåaÊèíÂÖ•Âà∞bÁöÑ‰ΩçÁΩÆÔºåb‰πãÂâçÁöÑÂÖÉÁ¥†ÈÉΩÊØîËæÉÂÆå(ÂÜÖÂæ™ÁéØÂÆå)ÔºåbÊèíÂÖ•Âà∞ÂâçÈù¢ÊâÄÊØîËæÉÁöÑÂÖÉÁ¥†‰∏≠ÊúÄÂ∞èÁöÑ‰ΩçÁΩÆ === //
 * @param {Array} arr - Êï∞ÁªÑ
 * @param {Boolean} sequence - true‰∏∫Ê≠£Â∫èÔºåfalse‰∏∫ÂÄíÂ∫è
 * @param {Array} property - ÊéíÂ∫èÊó∂ÊØîËæÉÁöÑÂ±ûÊÄßÂêç
 * @return {Array}
 */
export function insertionSort (arr, property, sequence) {
  arr = deepCopy(arr)

  const judge = (sequence, property, arr, inner, tmp) => {
    if (sequence === true) {
      return inner > 0 && findProperty(arr[inner - 1], property) > findProperty(tmp, property)
    } else {
      return inner > 0 && findProperty(arr[inner - 1], property) < findProperty(tmp, property)
    }
  }

  let outer
  let inner
  let len = arr.length
  let tmp

  for (outer = 1; outer < len; ++outer) {
    inner = outer
    tmp = arr[outer]

    while (judge(sequence, property, arr, inner, tmp)) {
      arr[inner] = arr[inner - 1]
      --inner
    }

    arr[inner] = tmp
  }

  return arr
}

/**
 * ÈÄâÊã©ÊéíÂ∫è
 * // === ÊÄùÊÉ≥Ôºö[a, b, c, d, e], Â§ñÂæ™ÁéØÂÖàÈÄâÊã©a‰∏∫ÊúÄÂ∞èÔºåÂÜÖÂæ™ÁéØ‰ªébÂºÄÂßã‰∏éaÊØîËæÉÔºåËã•ÊúâÊØîaÊõ¥Â∞èÁöÑÔºåËÆ∞ÂΩïÁ¥¢ÂºïÔºõÂÜÖÂæ™ÁéØÁªìÊùüÂêéÂ∞ÜÂ§ñÂæ™ÁéØÈÄâÊã©ÁöÑÂÖÉÁ¥†(ËÆ§ÂÆöÁöÑÊúÄÂ∞èÂÄº)‰∏éÊúÄÂêéÁöÑÁ¥¢ÂºïÊØîËæÉÔºåËã•‰∏ç‰∏ÄËá¥Âàô‰∫§Êç¢‰ΩçÁΩÆ === //
 * @param {Array} arr - Êï∞ÁªÑ
 * @param {Boolean} sequence - true‰∏∫Ê≠£Â∫èÔºåfalse‰∏∫ÂÄíÂ∫è
 * @param {Array} property - ÊéíÂ∫èÊó∂ÊØîËæÉÁöÑÂ±ûÊÄßÂêç
 * @return {Array}
 */

export function selectSort (arr, property, sequence) {
  arr = deepCopy(arr)

  const judge = (sequence, property, arr, inner, target) => {
    if (sequence === true) {
      return findProperty(arr[inner], property).localeCompare(findProperty(arr[target], property)) < 0
    } else {
      return findProperty(arr[inner], property).localeCompare(findProperty(arr[target], property)) > 0
    }
  }

  const len = arr.length
  let outer
  let inner
  let target

  for (outer = 0; outer < len - 1; ++outer) {
    target = outer

    for (inner = outer + 1; inner < len; ++inner) {
      if (judge(sequence, property, arr, inner, target)) {
        target = inner
      }
    }

    if (target !== outer) {
      swap(arr, target, outer)
    }
  }

  return arr
}

/**
 * Âø´Êéí
 * // === ÊÄùÊÉ≥Ôºö[a, b, c, d, e], Âà©Áî®ÂàÜÊ≤ªÂèëÔºåÂÆöc‰∏∫‰∏≠Èó¥ÂÄºÔºåÁÑ∂ÂêéÂàÜÂà´‰ªéaÂíåeÂêëcÈÅçÂéÜÔºåÂàÜÂà´ÊâæÂà∞Â∑¶ËæπÊØîcÂ§ß, Âè≥ËæπÊØîcÂ∞èÁöÑÂÄºÔºåÊØîÂ¶Ça,e; ÁÑ∂Âêé‰∫§Êç¢‰ΩçÁΩÆ, ‰æùÊ¨°Á±ªÊé®ÔºõÊúÄÂêé‰∏ÄÂÆöÊòØcÂ∑¶ËæπÁöÑÂÄºÊØîcÂ∞èÔºåÂè≥ËæπÁöÑÂÄºÈÉΩÊØîcÂ§ßÔºõÂÜçÂàÜÂà´ÂØπcÂ∑¶ËæπÁöÑÂÄºÂíåÂè≥ËæπÁöÑÂÄºÁî®ÂêåÊ†∑ÁöÑÂèñ‰∏≠Èó¥ÂÄºÊØîËæÉÁöÑÊñπÂºèÈÄêÂ±ÇÁöÑÈÄíÂΩíÁõ¥Âà∞ÂàíÂàÜÂá∫Êù•ÈúÄË¶ÅÊØîËæÉÁöÑÁ≥ªÂàó‰πãÁöÑÈïøÂ∫¶Âè™Êúâ1‰∏∫Ê≠¢ === //
 * @param {Array} arr - Êï∞ÁªÑ
 * @param {Boolean} sequence - true‰∏∫Ê≠£Â∫èÔºåfalse‰∏∫ÂÄíÂ∫è
 * @param {Array} property - ÊéíÂ∫èÊó∂ÊØîËæÉÁöÑÂ±ûÊÄßÂêç
 * @return {Array}
 */

export function quickSort (arr, property, sequence) {
  arr = deepCopy(arr)

  const judge = (sequence, property, arr, index, middle) => {
    if (sequence === true) {
      return new Date(findProperty(arr[index], property)).getTime() < new Date(findProperty(arr[middle], property)).getTime()
    } else {
      return new Date(findProperty(arr[index], property)).getTime() > new Date(findProperty(arr[middle], property)).getTime()
    }
  }

  const partition = (arr, left, right) => {
    const middle = (left + right) >> 1
    let i = left
    let j = right

    while (i <= j) {
      while (judge(sequence, property, arr, i, middle)) {
        ++i
      }

      while (judge(!sequence, property, arr, j, middle)) {
        --j
      }

      if (i <= j) {
        swap(arr, i, j)
        ++i
        --j
      }
    }

    return i
  }

  const quick = (arr, left, right) => {
    let middle

    if (arr.length > 1) {
      middle = partition(arr, left, right)

      if (left < middle - 1) {
        quick(arr, left, middle - 1)
      }

      if (middle < right) {
        quick(arr, middle, right)
      }
    }

    return arr
  }

  return quick(arr, 0, arr.length - 1)
}

/**
 * ÂΩíÂπ∂
 * // === ÊÄùÊÉ≥Ôºö[a, b, c, d ,e], Áî®ÂàÜÊ≤ªÂàíÂá∫Â∑¶Âè≥Êï∞ÁªÑÔºåÈÄíÂΩíÁõ¥Âà∞ÈïøÂ∫¶‰∏∫1ÔºõÊØîÂ¶Ça‰∏ébÊØîËæÉÔºåc‰∏édÊØîËæÉÔºåÊéíÂ∫èÂêéÊîæÂÖ•Êñ∞Êï∞ÁªÑËøîÂõûÔºå‰πãÂêéÔºåcd‰∏éeÊØîËæÉÔºåab‰∏écdeÊØîËæÉ === //
 * @param {Array} arr - Êï∞ÁªÑ
 * @param {Boolean} sequence - true‰∏∫Ê≠£Â∫èÔºåfalse‰∏∫ÂÄíÂ∫è
 * @param {Array} property - ÊéíÂ∫èÊó∂ÊØîËæÉÁöÑÂ±ûÊÄßÂêç
 * @return {Array}
 */

function merge (left, right, property, sequence) {
  const judge = () => {
    if (sequence === true) {
      return findProperty(left[0], property) < findProperty(right[0], property)
    } else {
      return findProperty(left[0], property) > findProperty(right[0], property)
    }
  }

  let ret = []

  while (left.length > 0 && right.length > 0) {
    if (judge()) {
      ret.push(left.shift())
    } else {
      ret.push(right.shift())
    }
  }

  while (left.length > 0) {
    ret.push(left.shift())
  }

  while (right.length > 0) {
    ret.push(right.shift())
  }

  return ret
}

function _mergeSort (arr, property, sequence) {
  const l = arr.length
  if (l === 1) {
    return arr
  }

  const middle = l >> 1
  let left = arr.slice(0, middle)
  let right = arr.slice(middle, arr.length)

  return merge(_mergeSort(left, property, sequence), _mergeSort(right, property, sequence), property, sequence)
}

export function mergeSort (arr, property, sequence) {
  arr = deepCopy(arr)

  return _mergeSort(arr, property, sequence)
}

/**
 * Â∏åÂ∞î
 * // === ÊÄùÊÉ≥Ôºö[a, b, c, d, e], ÂÖàËÆ°ÁÆóÂá∫Âä®ÊÄÅÈó¥Èöî, ÊØîÂ¶Çh = h * 3 + 1ÔºåÁÑ∂ÂêéÂ§ñÂæ™ÁéØ‰ªéhÂºÄÂßãÈÄê‰∏ÄÈÅçÂéÜÔºåÂÜÖÂæ™ÁéØÂ∞ÜÂ§ñÂæ™ÁéØÈÄâÂÆöÁöÑÂÖÉÁ¥†ÂèäÂÖ∂Ââçh‰ΩçÁΩÆÁöÑÂÖÉÁ¥†ÊØîËæÉÔºåÂΩìÂ§ñÂæ™ÁéØÈÅçÂéÜÂà∞ÊúÄÂêéÊó∂ÔºåÂÜçÊ¨°Âä®ÊÄÅËÆ°ÁÆóh‰∏∫(h - 1) / 3, Âç≥‰ª•Êõ¥Â∞èÁöÑÈó¥ÈöîÈáçÂ§çÂàöÊâçÁöÑÊìç‰ΩúÔºåÁü•ÈÅìh‰∏∫1 === //
 * // === ‰ºòÂäøÔºöËæÉÂø´Âú∞Â∞ÜËæÉËøúÁöÑÂÖÉÁ¥†ÂΩíÂà∞Ê≠£Á°ÆÁöÑ‰ΩçÁΩÆ === //
 * @param {Array} arr - Êï∞ÁªÑ
 * @param {Boolean} sequence - true‰∏∫Ê≠£Â∫èÔºåfalse‰∏∫ÂÄíÂ∫è
 * @param {Array} property - ÊéíÂ∫èÊó∂ÊØîËæÉÁöÑÂ±ûÊÄßÂêç
 * @return {Array}
 */

export function shellSort (arr, property, sequence) {
  arr = deepCopy(arr)

  const judge = function (arr, inner, h, sequence, property) {
    if (sequence === true) {
      return findProperty(arr[inner], property) < findProperty(arr[inner - h], property)
    } else {
      return findProperty(arr[inner], property) > findProperty(arr[inner - h], property)
    }
  }

  const len = arr.length
  let h = 1

  while (h < len / 3) {
    h = h * 3 + 1
  }

  while (h >= 1) {
    let outer
    let inner

    for (outer = h; outer < len; ++outer) {
      for (inner = outer; inner >= h && judge(arr, inner, h, sequence, property); inner -= h) {
        swap(arr, inner, inner - h)
      }
    }

    h = (h - 1) / 3
  }

  return arr
}
